@import "../css/normalize.scss"
@import "../css/reset.scss"
@import "../css/fonts.sass"
@import "../css/fontello.scss"

*
  box-sizing: border-box

$accent-color: #702283
$hover-color: #5b2069
$text-color: #333
$gray-color: #4f4f4f
$nav-color: #828282
$bg-color: #f8f8f8
$white-color: #fff
$sale-price-color: #ff0000

.title-h2
  font-family: "RussoOne"
  font-weight: 400
  font-size: 42px
  text-align: center

.title-h3
  font-family: "RussoOne"
  font-weight: 400
  font-size: 24px

.title-h4
  font-family: "RussoOne"
  font-weight: 400
  font-size: 22px

.title-h5
  font-family: "RussoOne"
  font-weight: 400
  font-size: 18px
  color: $white-color

.title-h6
  font-family: "RussoOne"
  font-weight: 400
  font-size: 16px
  color: $white-color

html
  font-family: "Helvetica"
  font-weight: 400
  font-size: 16px
  line-height: 130%

body
  background-color: $bg-color
  color: $text-color

.container,
.wrapper
  max-width: 1170px
  margin: 0 auto

.wrapper
  display: flex
  align-items: center

// Шапка сайта начало
.header
  min-height: 178px
  &__mobile
    display: none
  &__top
    padding: 13px 0
    &-lang
      margin-right: 95px
    &-select
      background-color: transparent
      font-size: 14px
      color: $gray-color
      border: none
      cursor: pointer
      option
        margin-bottom: 25px
    &-list
      display: flex
      flex-direction: row
      justify-content: space-between
      align-items: center
      list-style: none
      margin-right: 302px
    &-item
      margin-right: 30px
    &-item:last-child
      margin-right: 0
    &-link
      text-decoration: none
      font-size: 14px
      color: $nav-color
    &-link:hover
      color: $hover-color
    &-currency,
    &-reg
      display: flex
      justify-content: space-between
      align-items: center
    &-entrance,
    &-registration
      background-color: transparent
      font-size: 14px
      border: none
      color: $gray-color
      cursor: pointer
    &-entrance:hover,
    &-registration:hover
      color: $hover-color
    &-line
      width: 18px
      border: 1px solid #BDBDBD
      transform: rotate(90deg)
      padding: 0 8px
  &__middle
    background-color: #fff
    border-bottom: 1px solid #EFEFEF
    &-logo
      margin-right: 30px
      padding: 20px 0
    &-search
      position: relative
      margin-right: 30px
    &-input
      width: 550px
      height: 51px
      border-radius: 5px
      background-color: #eee
      padding: 0 15px
    &-img
      position: absolute
      top: 18px
      right: 23px
    &-contact
      margin-right: 40px
    &-phone>a
      font-family: "RussoOne", sans-serif
      font-size: 20px
      color: $gray-color
    &-link
      font-size: 14px
      color: $gray-color
    &-link:hover
      color: $hover-color
      text-decoration: none
    &-btn
      width: 170px
      height: 51px
      background-color: $accent-color
      border: none
      border-radius: 5px
      font-family: "RussoOne"
      font-size: 16px
      letter-spacing: 1px
      color: $white-color
      cursor: pointer
      span
        display: inline-block
        width: 36px
        height: 25px
        background-color: #fff
        color: $gray-color
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.1)
        border-radius: 5px
        margin: 0 15px 0 20px
        padding-top: 4px
    &-btn:hover
      background-color: $hover-color
      color: $nav-color
      box-shadow: 2px 2px 2px 1px rgba(0, 0, 255, .2)
      span
        background-color: $nav-color

  &__bottom
    min-height: 47px
    background-color: #fff
    &-list
      display: flex
      justify-content: space-between
      align-items: center
      list-style: none
      margin-right: 40px
    &-item
      margin-right: 42px
    &-item:last-child
      margin-right: 0
    &-link
      text-decoration: none
      font-size: 16px
      color: $text-color
      margin-right: 4px
    &-link:hover
      color: $hover-color
    &-link.active
      color: $accent-color
    &-link.close::after,
    &-link.active::after
      content: ''
      display: inline-block
      width: 15px
      height: 15px
      background-image: url('../img/icons/arrow-down.svg')
      background-repeat: no-repeat
      background-position: center
    &-link.active::after
      transform: rotate(180deg)
      color: $accent-color
    &-btn
      width: 116px
      height: 46px
      border: none
      font-weight: 700
      font-size: 16px
      line-height: 130%
      color: $accent-color
      text-transform: uppercase
      cursor: pointer
    &-btn:hover
      color: #ad125b
      background-color: $nav-color

.currency
  margin-right: 50px
  font-size: 14px
  line-height: 130%
  position: relative
  cursor: pointer
  .close
    color: #BDBDBD
  .active
    color: $gray-color
  &__uan
    margin-right: 6px
  &__btn
    width: 31px
    height: 18px
    margin-right: 6px
    background-color: #E0E0E0
    border-radius: 100px
    span
      display: inline-block
      position: absolute
      top: 2px
      right: 52px
      width: 14px
      height: 14px
      background-color: $accent-color
      border-radius: 100px
  &__btn:hover
    span
      background-color: $nav-color
  // Шапка сайта конец

// Баннер начало
.banner
  max-height: 550px
  // display: flex
  // justify-content: space-between
  // align-items: center
  background: url('../img/main_bg.png')center/cover no-repeat
  &__left
    padding-top: 160px
    padding-bottom: 163px
    &-title
      font-family: "RussoOne"
      font-size: 52px
      color: $white-color
      margin-bottom: 23px
    &-text
      color: #bdbdbd
      margin-bottom: 63px
    &-btn
      width: 270px
      height: 57px
      background: $accent-color
      border-radius: 5px
      font-weight: 700
      font-size: 16px
      color: #F5F5F5
      cursor: pointer
    &-btn:hover
      background-color: $hover-color
      color: $nav-color
// Баннер конец
// Шапка сайта конец

// Раздел популярное начало
.popular
  text-align: center
  position: relative
  &__bg
    position: absolute
    left: 0
    bottom: -100px
    z-index: -1
  &__title
    padding-top: 100px
    margin-bottom: 50px
  &__grid
    display: grid
    grid-template-columns: repeat(3, 1fr)
    grid-template-rows: repeat(2, 200px)
    grid-template-areas: "left center top" "left center bot"
    justify-items: space-between
    grid-gap: 30px
    position: relative
    margin-bottom: 30px
    &-item
      height: 430px
    &-item.item1
      grid-area: left
    &-item.item2
      grid-area: center
    &-item.item3
      grid-area: top
    &-item.item4
      grid-area: bot
    &-title
      text-align: left
      padding-top: 30px
      padding-left: 30px
      margin-bottom: 10px
    &-text
      text-align: left
      padding-left: 30px
    &-img_big
      padding-top: 40px
      position: relative
      z-index: 2
    &-img_small
      position: absolute
      left: 150px
      bottom: 140px
    &-fig_left
      position: absolute
      top: 0
      left: 0
      width: 470px
      border-top: 430px solid #fff
      border-right: 100px solid transparent
      z-index: -1
    &-img_black
      padding-top: 33px
      position: relative
      z-index: 2
    &-img_white
      position: absolute
      left: 540px
      bottom: 3px
    &-fig_center
      position: absolute
      top: 0
      left: 450px
      width: 280px
      height: 430px
      transform: skew(-13deg)
      background-color: #fff
      z-index: -1
    &-img_wheel
      position: absolute
      top: 35px
      right: 10px
    &-fig_right-top
      position: absolute
      top: 0
      right: 0
      width: 407px
      border-bottom: 200px solid #fff
      border-left: 44px solid transparent
      z-index: -1
    &-img_sim
      position: absolute
      bottom: 0
      right: 5px
    &-fig_right-bot
      position: absolute
      bottom: 0
      right: 0
      width: 457px
      border-bottom: 200px solid #fff
      border-left: 44px solid transparent
      z-index: -1
  &__btn
    width: 260px
    height: 57px
    background: $accent-color
    border-radius: 5px
    font-weight: 700
    font-size: 16px
    line-height: 130%
    text-align: center
    color: #FFFFFF
    cursor: pointer
  &__btn:hover
    background-color: #5b2069
    color: $nav-color

.title__overalls
  padding-left: 130px
.title__wheel
  padding-left: 45px
.title__sim
  margin-right: 260px
// Раздел популярное конец

// Раздел хиты начало
.selling
  padding-bottom: 100px
  &__title
    padding-top: 100px
    margin-bottom: 50px
  &__categories
    &-list
      margin-bottom: 30px
      display: flex
      justify-content: space-between
      align-items: center
    &-item
      width: 230px
      height: 47px
      transform: skew(-15deg)
      background-color: #fff
      margin-right: 5px
      padding: 14px 60px
      border: 1px solid #F2F2F2
      border-radius: 5px
      font-style: normal
      color: #828282
      cursor: pointer
    &-item>span
      display: inline-block
      transform: skew(15deg)
    &-item:last-child
      margin-right: 0
    &-item.active
      background-color: $accent-color
      color: $white-color
    &-item:hover
      background-color: $hover-color
      color: $nav-color
  &__grid
    display: grid
    grid-template-columns: repeat(3, 1fr)
    justify-items: space-between
    gap: 30
    margin-bottom: 30px
    &-item
      max-width: 370px
      height: 583px
      background-color: #fff
    &-image
      margin-bottom: 20px
      position: relative
      padding: 30px 30px 0px 30px
    &-sale
      position: absolute
      width: 60px
      height: 29px
      transform: skew(-15deg)
      background-color: $sale-price-color
      font-family: "RussoOne"
      font-weight: 400
      font-size: 14px
      line-height: 130%
      color: $white-color
      border-radius: 5px
      padding: 7px 12px
    &-sale>span
      display: inline-block
      transform: skew(15deg)
    &-sale.close
      display: none
    &-code
      padding: 0 30px
      margin-bottom: 10px
    &-title
      padding: 0 30px
      margin-bottom: 20px
    &-description
      padding: 0 30px
      display: flex
      justify-content: space-between
      align-items: center
      margin-bottom: 20px
    &-text
      font-size: 14px
      line-height: 130%
      margin-bottom: 10px
    &-size
      margin-right: 20px
    &-select
      width: 145px
      height: 45px
      cursor: pointer
    &-buy
      padding: 0 30px 30px
      display: flex
      justify-content: space-between
      align-items: center
    &-price
      font-family: 'RussoOne'
      font-weight: 400
      font-size: 22px
      line-height: 130%
    &-cart
      width: 144px
      height: 61px
      background-color: $accent-color
      border-radius: 5px
      line-height: 130%
      text-align: center
      color: $white-color
    &-cart:hover
      background-color: $hover-color
      color: $nav-color
      cursor: pointer
  &__button
    text-align: center

.price-sale
  color: $sale-price-color
.price-text
  display: block
  font-family: 'RussoOne'
  font-weight: 400
  font-size: 14px
  line-height: 130%
  text-decoration: line-through
// Раздел хиты конец

// Раздел скидки начало
.discounts
  margin-bottom: 100px
  &__title
    padding-top: 100px
    margin-bottom: 50px
  &__flex
    display: flex
    justify-content: space-between
    align-items: center
    position: relative
    &-prev,
    &-next
      display: inline-block
      position: absolute
      top: 50%
      width: 30px
      height: 30px
      text-align: center
      border-radius: 5px
      cursor: pointer
    &-prev
      left: -50px
    &-next
      right: -35px
    &-prev>i,
    &-next>i
      display: inline-block
      position: absolute
      top: 8px
      left: 3px
      text-align: center
    &-prev
      transform: rotate(90deg)
    &-next
      transform: rotate(-90deg)
    &-prev.close,
    &-next.close
      border: 1px solid #bdbdbd
      color: #bdbdbd
    &-prev.active,
    &-next.active
      border: 1px solid $gray-color
      color: $gray-color
    &-item
      max-width: 370px
      height: 583px
      background-color: #fff
  &__slider
    display: flex
    align-items: center
  &__slider>.slider-number
    display: none
// Раздел скидки конец

// Раздел О магазине начало
.about
  padding-top: 100px
  position: relative
  &__bg
    position: absolute
    left: 0
    bottom: -150px
    z-index: -1
  &__flex
    display: flex
    justify-content: space-between
    align-items: flex-start
    &-left
      margin-right: 40px
    &-img
      width: 470px
      height: 603px
      margin-bottom: 20px
    &-right
      position: relative
    &-fig
      position: absolute
      top: 50px
      right: 0
      width: 900px
      border-top: 610px solid #fff
      border-right: 140px solid transparent
      z-index: -1
    &-title
      text-align: left
      padding-top: 70px
      margin-bottom: 15px
    &-text
      max-width: 530px
      margin-bottom: 25px
      font-size: 18px
      line-height: 150%
      color: #828282
    &-text>span
      display: block
      padding: 10px 0
    &-next>a
      font-weight: 700
      text-decoration-line: underline
      color: $accent-color
    &-next>a:hover
      color: $hover-color
      text-decoration: none

.nav
  display: flex
  align-items: center
  &-prev,
  &-next
    display: inline-block
    width: 30px
    height: 30px
    position: relative
    text-align: center
    border-radius: 5px
    cursor: pointer
  &-prev>i,
  &-next>i
    display: inline-block
    position: absolute
    top: 8px
    left: 3px
    text-align: center
  &-prev
    margin-right: 16px
    transform: rotate(90deg)
  &-next
    margin-left: 16px
    transform: rotate(-90deg)
  &-number
    font-family: 'RussoOne'
    font-size: 20px
  &-number>span
    font-family: 'RussoOne'
    font-size: 14px
  &-prev.close,
  &-next.close
    border: 1px solid #bdbdbd
    color: #bdbdbd
  &-prev.active,
  &-next.active
    border: 1px solid $gray-color
    color: $gray-color
// Раздел О магазине конец

// Раздел бренды начало
.brands
  padding-bottom: 140px
  &__title
    padding-top: 100px
    margin-bottom: 50px
  &__flex
    display: flex
    justify-content: space-between
    align-items: center
    &-item
      width: 170px
      height: 98px
      margin-right: 30px
    &-item:last-child
      margin-right: 0
// Раздел бренды конец

// Подвал начало
.footer
  padding-top: 60px
  padding-bottom: 80px
  background-color: #181818
  &__wrapper
    display: flex
    justify-content: space-between
  &__socials
    &-logo
      margin-bottom: 20px
    &-title
      margin-bottom: 10px
    &-text
      width: 270px
      margin-bottom: 17px
      color: #BDBDBD
    &-icons
      margin-bottom: 170px
      color: #828282
    &-list,
    &-icons
      display: flex
      align-items: center
      justify-content: start
    &-icons>p
      margin-right: 15px
    &-link
      color: #828282
      text-decoration: none
    &-item
      margin-right: 18px
    &-link:hover
      color: $hover-color
    &-copy
      width: 270px
      color: #828282
  &__catalog
    &-title
      margin-bottom: 20px
    &-list
      margin-bottom: 35px
    &-item
      margin-bottom: 20px
    &-link
      color: #E0E0E0
      text-decoration: none
    &-link:hover
      color: $hover-color
    &-sale>a
      font-weight: 700
      color: #E0E0E0
      text-decoration: none
    &-sale>a:hover
      color: $hover-color
  &__additionally
    margin-bottom: 65px
    &-title
      margin-bottom: 20px
    &-item
      margin-bottom: 20px
    &-link
      color: #E0E0E0
      text-decoration: none
    &-link:hover
      color: $hover-color
  &__cabinet
    &-title
      margin-bottom: 20px
    &-item
      margin-bottom: 20px
    &-link
      color: #E0E0E0
      text-decoration: none
    &-link:hover
      color: $hover-color
  &__contacts
    margin-bottom: 78px
    &-title
      margin-bottom: 20px
    &-item
      margin-bottom: 20px
    &-link,
    &-link>i
      color: $white-color
      text-decoration: none
    &-link>i
      color: #828282
      margin-right: 5px
    &-link:hover,
    &-link>i:hover
      color: $hover-color
    &-text
      margin-top: 8px
    &-text>a
      font-size: 14px
      text-decoration: underline
      color: #828282
      padding-left: 35px
      cursor: pointer
    &-text>a:hover
      color: $hover-color
      text-decoration: none
  &__news
    &-title
      margin-bottom: 15px
    &-text
      color: #BDBDBD
      margin-bottom: 25px
    &-email
      box-sizing: border-box
      width: 230px
      height: 57px
      background: #333333
      border: 1px solid #4F4F4F
      border-radius: 5px
      color: $white-color
      padding-left: 16px
      margin: 0
    &-submit
      width: 140px
      height: 57px
      background-color: $accent-color
      border-radius: 5px
      border: none
      color: $white-color
      cursor: pointer
    &-submit:hover
      background-color: $hover-color
      color: $nav-color
// Подвал конец

// Медиа-запросы
@media screen and (max-width: 321px)
  .container
    width: 320px
    margin: 0
    padding: 0
  .header__desktop
    display: none
  .header__mobile
    display: block
    &-top,
    &-bot
      display: flex
      justify-content: space-between
      align-items: center
      height: 52px
      padding: 8px 16px
    &-bot
      background-color: #fff
    &-contacts
      width: 46px
      height: 34px
      background: #FFFFFF
      border: 1px solid #F2F2F2
      border-radius: 5px
      padding: 7px 0 0 12px
    &-user
      width: 46px
      height: 34px
      background: #FFFFFF
      border: 1px solid #F2F2F2
      border-radius: 5px
      padding: 7px 0 0 12px
    &-menu
      display: block
      overflow: hidden
      margin: 0
      padding: 0
      width: 32px
      height: 24px
      font-size: 0
      background-color: transparent
      position: relative
      cursor: pointer
      span
        display: block
        position: absolute
        background-color: $accent-color
        height: 3px
        width: 32px
        top: 10px
        left: 0
        border-radius: 2px
        transition: background 0s 0.3s
      span::after,
      span::before
        content: ""
        display: block
        width: 100%
        height: 3px
        background-color: $accent-color
        position: absolute
        border-radius: 2px
        transition-duration: 0.3s, 0.3s
        transition-delay: 0.3s, 0s
      span::after
        top: -8px
      span::before
        bottom: -8px
    &-menu.close span
      background: none
    &-menu.close span::after
      top: 0px
      transform: rotate(45deg)
    &-menu.close span::before
      bottom: 0px
      transform: rotate(-45deg)
    &-input
      width: 170px
      height: 34px
      background: #EEEEEE
      border-radius: 5px
      position: relative
      border: none
      padding-left: 10px
    &-search>img
      position: absolute
      right: 90px
      top: 70px
    &-cart
      width: 46px
      height: 34px
      background: $accent-color
      border-radius: 5px
      padding: 7px 0 0 12px
  .header__contacts-nav
    display: none
  .banner
    max-height: 450px
    background: url('../img/bg-mobile.png')center/cover no-repeat
    &__left
      padding: 236px 16px 48px
      &-title
        font-size: 26px
        margin-bottom: 13px
      &-text
        margin-bottom: 24px
      &-btn
        width: 228px
        height: 53px
  .popular
    &__title
      font-size: 22px
      padding-top: 48px
      margin-bottom: 24px
    &__grid
      grid-template-columns: repeat(1, 1fr)
      grid-template-rows: repeat(4, 264px)
      gap: 16px
      grid-template-areas: "top" "center" "mid" "bot"
      padding: 0 16px
      &-item
        width: 288px
        height: 264px
        padding-left: 16px
        padding-top: 16px
        background-color: #fff
        text-align: left
      &-item.item1
        grid-area: top
      &-item.item2
        grid-area: center
      &-item.item3
        grid-area: mid
      &-item.item4
        grid-area: bot
      &-title.title-h3,
      &-title.title-h4
        padding: 0
        margin-bottom: 8px
        font-size: 18px
      &-text
        padding: 0
        text-align: left
      &-img_big
        padding: 0
        margin-top: 46px
        padding-bottom: 16px
        width: 158px
        z-index: 2
      &-img_small
        width: 93px
        height: 93px
        position: absolute
        left: 120px
        top: 85px
      &-img_black
        padding: 0
        width: 101px
        height: 196px
        margin-left: 60px
        margin-top: 10px
        z-index: 2
      &-img_white
        width: 85px
        height: 173px
        left: 130px
        top: 370px
      &-img_wheel
        width: 220px
        height: 160px
        position: absolute
        top: 664px
        left: 40px
      &-img_sim
        position: absolute
        bottom: 0
        left: 35px
      &-fig_left,
      &-fig_center,
      &-fig_right-top,
      &-fig_right-bot
        display: none
    &__btn
      width: 288px
      height: 53px
      margin-bottom: 48px

  .title__overalls,
  .title__wheel,
  .title__sim
    padding: 0

  .selling
    padding-bottom: 0
    &__title
      padding: 0
      width: 288px
      font-size: 22px
      margin-bottom: 24px
    &__categories
      &-list
        margin: 0 16px 16px
        justify-content: flex-start
        align-items: center
      &-item
        width: 180px
        height: 40px
        font-size: 14px
        margin-right: 10px
